import React, { useEffect, useRef, useState } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import AnimatedCursor from "react-animated-cursor";
import styles from "./Project.module.scss";

gsap.registerPlugin(ScrollTrigger);

const Project = () => {
  const galleryRef = useRef(null);
  const rightRef = useRef(null);
  const projectRefs = useRef([]);
  const photoRefs = useRef([]);

  const projects = [
    {
      title: "3on3",
      description:
        "   .         .",
      color: "crimson",
      skills: [
        "Java",
        "Spring Boot",
        "MySQL",
        "Redis",
        "AWS(EC2)",
        "AWS(S3)",
        "AWS(RDS)",
        "Docker",
        "JPA",
        "React",
      ],
    },
    {
      title: " ",
      description:
        "                  ",
      color: "MediumSeaGreen",
      skills: [
        "Java",
        "Spring Boot",
        "MySQL",
        "HTML/CSS",
        "MyBatis",
        "WebSocket",
      ],
    },
    // {
    //   title: "AI Chat",
    //   description:
    //     "    .        .",
    //   color: "dodgerblue",
    //   skills: ["Python", "TensorFlow", "NLP"],
    // },
  ];

  const [cursorText, setCursorText] = useState("");
  const [cursorVariant, setCursorVariant] = useState("default");

  const onEnterFrame = () => {
    setCursorText("View");
    setCursorVariant("project");
    document.body.style.cursor = "none";
  };

  const onLeaveFrame = () => {
    setCursorText("");
    setCursorVariant("default");
    document.body.style.cursor = "auto";
  };

  useEffect(() => {
    let mm = gsap.matchMedia();

    mm.add("(min-width: 600px)", () => {
      ScrollTrigger.create({
        trigger: galleryRef.current,
        start: "top top",
        end: "bottom+=2% bottom", //  
        pin: rightRef.current,
      });

      gsap.set(photoRefs.current.slice(1), { yPercent: 101 });

      projectRefs.current.forEach((project, index) => {
        if (index === 0) return;

        let headline = project.querySelector("h1");
        let animation = gsap
          .timeline()
          .to(photoRefs.current[index], { yPercent: 0 })
          .set(photoRefs.current[index - 1], { autoAlpha: 0 });

        ScrollTrigger.create({
          trigger: headline,
          start: "top 80%",
          end: "top 40%", //     
          animation: animation,
          scrub: 1,
        });
      });

      return () => {
        ScrollTrigger.getAll().forEach((t) => t.kill());
      };
    });
  }, []);

  return (
    <div className={styles.projectContainer}>
      <AnimatedCursor
        innerSize={4}
        outerSize={30}
        color={cursorVariant === "project" ? "#eeff04" : "#ffffff"}
        innerScale={1}
        outerScale={1}
        outerAlpha={0}
        hasBlendMode={true}
        outerStyle={{
          border: `1.5px solid ${
            cursorVariant === "project" ? "#eeff04" : "#ffffff"
          }`,
        }}
        innerStyle={{
          backgroundColor: cursorVariant === "project" ? "#eeff04" : "#ffffff",
        }}
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
        ]}
        textColor={cursorVariant === "project" ? "#000000" : "#ffffff"}
        text={cursorText}
        showSystemCursor={false}
      />

      <div className={styles.spacer}></div>
      <div className={styles.gallery} ref={galleryRef}>
        <div className={styles.left}>
          <div className={styles.desktopContent}>
            {projects.map((project, index) => (
              <div
                key={index}
                className={styles.desktopContentSection}
                ref={(el) => (projectRefs.current[index] = el)}
              >
                <h1>{project.title}</h1>
                <p>{project.description}</p>
                <ul className={styles.skillsList}>
                  {project.skills.map((skill, skillIndex) => (
                    <li key={skillIndex}>{skill}</li>
                  ))}
                </ul>
                <button className={styles.exploreButton}>View Project</button>
              </div>
            ))}
          </div>
        </div>
        <div className={styles.right} ref={rightRef}>
          <div
            className={styles.desktopPhotos}
            onMouseEnter={onEnterFrame}
            onMouseLeave={onLeaveFrame}
          >
            {projects.map((project, index) => (
              <div
                key={index}
                className={`${styles.desktopPhoto} ${styles[project.color]}`}
                ref={(el) => (photoRefs.current[index] = el)}
              ></div>
            ))}
          </div>
        </div>
      </div>
      <div className={styles.spacer}></div>
    </div>
  );
};

export default Project;
