import React, { useEffect, useRef, useState } from "react";
import gsap from "gsap"; // GSAP 
import { ScrollTrigger } from "gsap/ScrollTrigger"; // ScrollTrigger 
import Intro from "./Intro";
import NavigationBar from "./NavigationBar";
import Introduce from "./Introduce";
import Skill from "./Skill";
import Project from "./Project";
import styles from "./MainPages.module.scss";
import MarqueeComponent from "./MarqueeComponent";
import MarqueeCh from "./MarqueeCh";
import ContactMe from "./ContactMe";

gsap.registerPlugin(ScrollTrigger); // ScrollTrigger  

const MainPages = () => {
  const containerRef = useRef(null);
  const navRef = useRef(null);
  const [isNavHidden, setIsNavHidden] = useState(false);

  useEffect(() => {
    // ScrollTrigger 
    ScrollTrigger.refresh();

    //   : Introduce     
    gsap.fromTo(
      containerRef.current,
      { backgroundColor: "#222" }, //  
      {
        backgroundColor: "#eeff04", //  
        // ease: "none", //   
        ease: "power2.inOut", //   
        scrollTrigger: {
          trigger: "#introduce", //  
          start: "top bottom", //    
          end: "top top", //    
          scrub: true, //    
        },
      }
    );

    //   : Skill       
    gsap.fromTo(
      containerRef.current,
      { backgroundColor: "#eeff04" }, //   
      {
        backgroundColor: "#222", //   ( )
        ease: "none", //   
        scrollTrigger: {
          trigger: "#skill", //   
          start: "top bottom", //    
          end: "top top", //    
          scrub: true, //    
        },
      }
    );

    //   
    let lastScrollTop = 0;

    const handleScroll = () => {
      const scrollTop =
        window.pageYOffset || document.documentElement.scrollTop;
      if (scrollTop > lastScrollTop && scrollTop > 100) {
        //   50px   
        setIsNavHidden(true);
      } else {
        //   50px  
        setIsNavHidden(false);
      }
      lastScrollTop = scrollTop <= 0 ? 0 : scrollTop;
    };

    window.addEventListener("scroll", handleScroll);

    // ScrollTrigger   
    ScrollTrigger.refresh();

    //      
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <>
      <div
        ref={navRef}
        className={`${styles.navContainer} ${isNavHidden ? styles.hidden : ""}`}
      >
        <NavigationBar />
      </div>
      <div ref={containerRef} className={styles.container}>
        <div id="intro" className={styles.section}>
          <Intro />
          <MarqueeComponent />
        </div>
        <div id="introduce" className={styles.section}>
          <Introduce />
        </div>
        <div id="skill" className={styles.section}>
          <Skill />
          <MarqueeCh
            direction="left"
            texts={["BACKEND", "&", "FRONTEND", "HUN"]}
            rotateAngle={-3}
          />
        </div>
        <div id="project" className={styles.section}>
          <Project />
        </div>
        <div id="screen5" className={styles.section1}>
          <ContactMe />
        </div>
      </div>
    </>
  );
};

export default MainPages;
